/**
 * 
 *  weixin-js-sdk
 *  makeShareLink 
 *  mixins 
 * */

import wx from 'weixin-js-sdk'
import {mapGetters} from 'vuex'

export default {
    name: "share",
    data() {
        return {
            share_info: {
                title: '',
                desc: '',
                img: this.$config.PROJECT_SHARE_REQUEST + '/h/' + this.$config.PROJECT_ID + '/dist/share.png'
            }
        }
    },
    async mounted() {

        // 
        let _share_url = await this.$utils.makeShareLink();
        this.$set(this.share_info, "link", _share_url);

        // 
        let _is_weixin = this.$utils.isWeiXin();

        if (_is_weixin) {
            this._initWeiXinShare(this.share_info);
        } else {
            console.log('')
        }

    },
    methods: {
        async _initWeiXinShare(info) {

            const _shareUrl = window.location.href;

            // 
            let _wx_config = await this.$store.dispatch('_getWeiXinConfig', {
                im: this.$config.PROJECT_INTERFACE.get_jsconf,
                fps: {
                    url: encodeURIComponent(_shareUrl)
                },
                url: this.$config.REQUEST_URL
            });

            wx.config({
                debug: false,
                appId: _wx_config.back_value.appId, // 
                timestamp: _wx_config.back_value.timestamp, // 
                nonceStr: _wx_config.back_value.nonceStr, // 
                signature: _wx_config.back_value.signature, // 1
                jsApiList: JSON.parse(_wx_config.back_value.jsApiList_json), //  JS JS2
            });

            wx.ready(function () {
                /**
                 * 
                 * */
                wx.onMenuShareAppMessage({
                    title: info.title, // 
                    desc: info.desc, // 
                    link: info.link, // 
                    imgUrl: info.img, // 
                    type: '', // ,musicvideolinklink
                    dataUrl: '', // typemusicvideo
                    success() {
                        console.log('')
                    },
                    cancel() {
                        console.log('')
                    }
                });
                /**
                 * 
                 * */
                wx.onMenuShareTimeline({
                    title: info.title, // 
                    desc: info.desc, // 
                    link: info.link, // 
                    imgUrl: info.img, // 
                    success() {
                        console.log('')
                    },
                    cancel() {
                        console.log('')
                    }
                });
            });

        }
    },
    computed: {
        ...mapGetters([])
    }
}
